<template>
	<div class="icons" >
		<swiper :options="swiperOption" >
			<swiper-slide v-for="(page,index) of pages" :key="index">
				<div class="icon" 
					 v-for="item of page"
					 :key='item.id'>
					<div class="icon-img">
						<img class="icon-img-Content" :src="item.imgUrl" >
					</div>
					<p class="icon-desc">{{item.desc}}</p>
				</div>
			</swiper-slide >	
		 </swiper>
	</div>
</template>

<script>
	export default {
		name :'HomeIcons',
		props:{
			  list: Array
		},
		data(){
			return {
				swiperOption:{
					autoplay:false
				}
			}
		},
		computed: {
			pages (){
				const pages=[]
				this.list.forEach((item,index)=>{
					//如果为0 则展示在第一页
					//如果为1 则展示在第2页
					const page=Math.floor(index/8)
					//如果数组中没有重复的页数 那么就创建新的页数的数组为空 之后往里面添加item
					if(!pages[page]){
						pages[page]=[]
				
					}
					pages[page].push(item)
					
					
				})
				
				return pages
			}
		}
	}
</script>

<style lang="stylus"  scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'

.icons >>> .swiper-container
	overflow:hidden
	height:0
	padding-bottom:50%
.icons
	margin-top:.2rem

	.icon
		overflow:hidden
		float:left
		width:25%
		height:0
		padding-bottom:25%
		position:relative
		.icon-img
			position:absolute
			top:0
			left:0
			right:0
			bottom:.44rem
			box-sizing: border-box
			.icon-img-Content
				display:block
				margin:0 auto
				height:100%
		.icon-desc
			position:absolute
			left:0
			right:0
			bottom:0
			height:.44rem
			text-align: center
			line-height:.44rem
			color:$darkTextColor
			ellipsis()





</style>


